:root {
  --tg-bg-color: #ffffff;
  --tg-text-color: #000000;
  --tg-hint-color: #707579;
  --tg-button-color: #2481cc;
  --tg-button-text-color: #ffffff;
}

.tg-theme-dark {
  --tg-bg-color: #18222d;
  --tg-text-color: #ffffff;
  --tg-hint-color: #aaaaaa;
  --tg-button-color: #2ea6ff;
  --tg-button-text-color: #ffffff;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: var(--tg-bg-color);
  color: var(--tg-text-color);
  transition: all 0.3s ease;
}

.app {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.model-list {
  width: 250px;
  border-right: 1px solid #e1e1e1;
  overflow-y: auto;
  padding: 10px;
  background-color: var(--tg-bg-color);
}

.model-header {
  padding: 10px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
}

.chat-list {
  padding-left: 15px;
}

.chat-item {
  padding: 8px;
  margin: 5px 0;
  cursor: pointer;
  border-radius: 5px;
}

.chat-item:hover,
.chat-item.active {
  background-color: var(--tg-button-color);
  color: var(--tg-button-text-color);
}

.new-chat-btn {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  background-color: var(--tg-button-color);
  color: var(--tg-button-text-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.chat-window {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.messages-container {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.input-area {
  display: flex;
  padding: 10px;
  border-top: 1px solid #e1e1e1;
}

.input-area input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  background-color: var(--tg-bg-color);
  color: var(--tg-text-color);
}

.input-area button {
  padding: 8px 15px;
  background-color: var(--tg-button-color);
  color: var(--tg-button-text-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.empty-chat {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.message {
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  max-width: 80%;
}

.message.user {
  margin-left: auto;
  background-color: var(--tg-button-color);
  color: var(--tg-button-text-color);
}

.message.bot {
  margin-right: auto;
  background-color: #e5e5ea;
  color: #000;
}

.message-content {
  word-wrap: break-word;
}

.message-time {
  font-size: 0.8em;
  opacity: 0.7;
  text-align: right;
  margin-top: 5px;
}